<template>
  <v-row>
    <v-col cols="12">
      <v-list one-line subheader flat>
        <v-list-item v-for='item in getListFilter' :key='item.title'>
          <v-list-item-avatar class='mt-0 mr-1'>
            <v-icon :class='getIconClass(item.PRIORIDADE)'/>
          </v-list-item-avatar>

          <v-list-item-content>
            <v-list-item-title v-html='getOrderTitle(item.DEF)'></v-list-item-title>
            <v-list-item-subtitle v-html='getOrderType(item.TBTIPOMANNOME)'></v-list-item-subtitle>
            <v-list-item-subtitle v-html='getOrderSector(item.TBLOC_INSTNOME)'></v-list-item-subtitle>
            <v-list-item-subtitle v-html='getOrderTypeOm(item.IDLAYOUT)'></v-list-item-subtitle>
          </v-list-item-content>          
       
        <v-tooltip bottom>
          <template v-slot:activator="{ on }">
            <v-btn v-if="!isHidden" fab color="primary" dark v-on="on" class="btnTooltip">
              <v-icon>mdi-alert-circle-outline</v-icon>
            </v-btn>             
          </template>
          <span v-html='item.OBS'></span>
        </v-tooltip>        

          <template>
              <v-speed-dial
                v-model="fab"
                :top="top"
                :bottom="bottom"
                :right="right"
                :left="left"
                :direction="direction"
                :open-on-hover="hover"
                :transition="transition"
              >
                <template v-slot:activator>
                  <v-btn
                    v-on:click="isHidden = !isHidden"
                    v-model="fab"
                    color="blue darken-2"
                    dark
                    fab
                  >
                    <v-icon v-if="fab">mdi-close</v-icon>
                    <v-icon v-else>mdi-format-list-bulleted-square</v-icon>
                  </v-btn>
                </template>
                <v-btn
                v-if="aceitarBtn"
                  v-on:click="isHidden = !isHidden"
                  @click='acceptOM(item.ID)'
                  fab
                  dark
                  small
                  color="green"
                >
                  <v-icon>mdi-check</v-icon>
                </v-btn>
               
                <v-btn
                 v-if="cargoBtn"
                 v-on:click="isHidden = !isHidden"
                 @click="dialog2 = !dialog2"          
                  fab
                  dark
                  small
                  color="red"
                >
                  <v-icon>mdi-account-arrow-right</v-icon>
                </v-btn>

                <v-btn
                 v-if="entrarBtn"
                 v-on:click="isHidden = !isHidden"
                 @click='acessarOM(item.ID)'         
                  fab
                  dark
                  small
                  color="blue darken-1"
                >
                  <v-icon>mdi-arrow-up-bold</v-icon>
                </v-btn>

              </v-speed-dial>
          </template>
          
          <template>
            <div>
              <v-row
                justify="center"
              >
                <v-dialog
                  v-model="dialog2"
                  max-width="500px"
                >
                  <v-card>
                    <v-card-title>
                      Selecione o manutentor para ser delegado:
                    </v-card-title>
                    <v-card-text>
                      <v-select
                        v-model="manutentor"
                        :items="selectAllManutentor"
                        label="Lista dos Manutentores"
                        
                      ></v-select>
                    </v-card-text>
                    <v-card-actions>
                      <v-btn
                        color="primary"
                        text
                        @click="dialog2 = false"
                      >
                        Cancelar
                      </v-btn>

                      <v-btn
                        color="green"
                        text
                        @click='delegateOM(item.ID)' 
                      >
                        Concluir
                      </v-btn>  
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-row>
            </div>
          </template>
          
        </v-list-item>
      </v-list>

      <v-overlay :value="loadingPage">
        <v-progress-circular indeterminate size="64"></v-progress-circular>
      </v-overlay>
    </v-col>
    <v-bottom-sheet v-model="showAlert">
      <v-sheet inset class="text-center" height="150px">
        <div class="py-1">{{ message.text }}</div>
        <v-btn
          class="mt-6"
          dark
          outlined
          color="red"
          @click="getAllOrderList()"
        >
          Tentar Novamente
        </v-btn>
      </v-sheet>
    </v-bottom-sheet>
  </v-row>
</template>

<script>
import { get, post, put, remove  } from '../utils/http.js';

export default {
  data() {
    return {
      entrarBtn:false,
      aceitarBtn:false,
      cargoBtn: false,
      loadingPage: false,
      showAlert: false,
      show: false,
      list: [],
      listOmAtribuida: [],
      listOmEmAnalise: [],
      selectAllManutentor: [],
      status: 0,
      message: {
        type: '',
        text: '',
        showReloadPage: false,
      },
      manutentor: 0,
      dialog2: false,
      isHidden: false,
      direction: 'left',
      fab: false,
      fling: false,
      hover: false,
      tabs: null,
      top: false,
      right: false,
      bottom: false,
      left: false,
      transition: 'slide-y-reverse-transition',
    }
  },

  mounted() {
    this.getAllOrderList();
    this.getAllManutentor();
    this.getOmAtribuida();
    this.getOmFinalizada();
    this.verificaBtn();
    
  },

  computed: {
    getListFilter() {
      //if (this.$store.state.list.searchFilter !== '') return this.list.filter(i => i.titulo.includes(this.$store.state.list.searchFilter));
      //if (this.$store.state.list.filterStatus === 0) return this.list;
      //return this.list.filter(i => i.ESTADO === this.$store.state.list.filterStatus);
      if(this.$store.state.list.filterStatus == 0){
        this.entrarBtn = false;
        this.aceitarBtn = true;
        return this.list;
      }
      else if(this.$store.state.list.filterStatus == 2){
         this.entrarBtn = true;
         this.aceitarBtn = false;
         return this.listOmAtribuida;
      } 
      else if(this.$store.state.list.filterStatus == 3){
        this.entrarBtn = false;
        this.aceitarBtn = false;
        return this.listOmEmAnalise;
      } 
    },
  },

  methods: {

   verificaBtn(){
     let cargo = localStorage.getItem('cargoId');

    if(this.$store.state.list.filterStatus == 2){
      
    }

     if( cargo != 1){
       this.cargoBtn = true;
     }
   },
    getOrderType(type) {
      return `Tipo de Manutenção: <b>${type}</b>`;
    },
      getOrderTitle(def) {
      return `Titulo: <b>${def}</b>`;
    },
      getOrderSector(local) {
      return `Local: <b>${local}</b>`;
    },
    getOrderTypeOm(typeOm) {
      if(typeOm == 1){
         return `Tipo da om: <b>Corretiva/Preventiva</b>`;
      }
      else if(typeOm == 3){
         return `Tipo da om: <b>Lista</b>`;
      }
      else if(typeOm == 2){
         return `Tipo da om: <b>Rota</b>`;
      }
     
    },

    acessarOM(idOm){
       this.$router.push(`/epi/${idOm}`);
    },

    getIconClass(priority) {
      if (priority === 1) return 'yellow'; 
      if (priority === 2) return 'orange'; 
      if (priority === 3) return 'red'; 
    },

   async getAllManutentor(){
      const idUser = localStorage.getItem('userId');

      post({ url: `${this.$host}:${this.$port}/GetManuts`,
       body: {
          idUser: idUser
        },
      }).then(res => {
        res.data.map(i => {
            this.selectAllManutentor.push({ value: i.ID, text: i.NOME });
          });
      }).catch(err => {
        alert(err);
      })
    },

   getOmAtribuida() {
      this.showAlert = false;
      this.loadingPage = true;
      this.message.text = '';
      const idUser = localStorage.getItem('userId');
      const cargoId = localStorage.getItem('cargoId');

      if(cargoId == 1){
        post({ url: `${this.$host}:${this.$port}/GetOMByUserIDAtribuida`,
          body: {
            idUser: idUser
          },
        }).then(res => {
          this.listOmAtribuida = res.data;
          this.loadingPage = false;
        }).catch(err => {
          this.loadingPage = false;
          this.message.text = 'Ocorreu um erro ao obter informações...';
          this.showAlert = true;
        })
      } 
      else if(cargoId == 2){
        post({ url: `${this.$host}:${this.$port}/GetOMsAndamentoLider`,
          body: {
            idUser: idUser
          },
        }).then(res => {
          this.listOmAtribuida = res.data;
          this.loadingPage = false;
        }).catch(err => {
          this.loadingPage = false;
          this.message.text = 'Ocorreu um erro ao obter informações...';
          this.showAlert = true;
        })
      }
      else if(cargoId == 3){
         get({ url: `${this.$host}:${this.$port}/GetOMsAndamentoADM`
        }).then(res => {
          this.listOmAtribuida = res.data;
          this.loadingPage = false;
        }).catch(err => {
          this.loadingPage = false;
          this.message.text = 'Ocorreu um erro ao obter informações...';
          this.showAlert = true;
        })
      }
      
    },

    getOmFinalizada() {
      this.showAlert = false;
      this.loadingPage = true;
      this.message.text = '';
      const idUser = localStorage.getItem('userId');
      const cargoId = localStorage.getItem('cargoId');

      if(cargoId == 1){
        post({ url: `${this.$host}:${this.$port}/GetOMByUserIDFinalizada`,
          body: {
            idUser: idUser
          },
        }).then(res => {
          this.listOmEmAnalise = res.data;
          this.loadingPage = false;
        }).catch(err => {
          this.loadingPage = false;
          this.message.text = 'Ocorreu um erro ao obter informações...';
          this.showAlert = true;
        })
      } 
      else if(cargoId == 2){
        post({ url: `${this.$host}:${this.$port}/GetOMsFinalizadaLider`,
          body: {
            idUser: idUser
          },
        }).then(res => {
          this.listOmEmAnalise = res.data;
          this.loadingPage = false;
        }).catch(err => {
          this.loadingPage = false;
          this.message.text = 'Ocorreu um erro ao obter informações...';
          this.showAlert = true;
        })
      }
      else if(cargoId == 3){
        get({ url: `${this.$host}:${this.$port}/GetOMsFinalizadaADM`,       
        }).then(res => {
          this.listOmEmAnalise = res.data;
          this.loadingPage = false;
        }).catch(err => {
          this.loadingPage = false;
          this.message.text = 'Ocorreu um erro ao obter informações...';
          this.showAlert = true;
        })
      }
      
    },


    async getAllOrderList() {
      this.showAlert = false;
      this.loadingPage = true;
      this.message.text = '';
      
      get({ url: `${this.$host}:${this.$port}/GetOM` }).then(res => {
        this.list = res.data;
        this.loadingPage = false;
      }).catch(err => {
        this.loadingPage = false;
        this.message.text = 'Ocorreu um erro ao obter informações...';
        this.showAlert = true;
      })
    },

    acceptOM(idOrder){
      const idUser = localStorage.getItem('userId');
      post({
        url: `${this.$host}:${this.$port}/AddOMUser`,
        body: {
          idUser: idUser,
          idOm: idOrder
        },
      }).then(res => {
        alert("Ordem de manutenção movida para sua lista.");      
      }).catch(err => {
        alert("Ocorreu um erro ao aceitar essa Ordem de manutenção");
      });
    },
    
     delegateOM(idOm){
      var idUser = localStorage.getItem("userId");
       post({
        url: `${this.$host}:${this.$port}/DelegateOM`,
        body: {
          idOm: idOm, //id da om
          idUser: this.manutentor ,
          idAdm: idUser,
        }
      }).then(res => {
        this.dialog2 = false
        alert("Manutentor delegado com sucesso.");      
      }).catch(err => {
        alert("Ocorreu um erro ao delegadar essa Ordem de manutenção");
      });
    }
  }
}
</script>

<style>
  .v-icon {
    width: 20px !important;
    height: 20px !important;
  }
  .btnTooltip{
    margin-right: 10px;
  }
</style>
