<template>
  <v-app>
    <v-navigation-drawer app v-model='drawer' v-if="!isLoginPage">
      <v-list-item>
        <v-list-item-avatar>
          <v-img src='data:image/png;base64,'></v-img>
        </v-list-item-avatar>

        <v-list-item-content>
          <v-list-item-title>Menu</v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <v-list dense>
        <v-list-item
          v-for='item in menu'
          :key='item.title'
          link
          :to='item.to'
        >
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar app dark color="red darken-3" v-if="!isLoginPage">
      <v-app-bar-nav-icon @click.stop='drawer = !drawer'/>
      <div class='flex-grow-1' />
      
      <v-btn v-if="isListPage" icon @click="showFilterPage = !showFilterPage">
        <v-icon>mdi-filter</v-icon>
      </v-btn>

      <v-menu offset-y>
        <template v-slot:activator="{ on }">
          <v-btn icon v-on="on">
            <v-icon>mdi-dots-vertical</v-icon>
          </v-btn>
        </template>
        <v-list>
          <v-list-item>
            <v-list-item-title>Meu Perfil</v-list-item-title>
          </v-list-item>
          <v-list-item>
            <v-list-item-title @click="logout()">Sair</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>

      <template v-if="isListPage" v-slot:extension>
        <v-tabs
          fixed-tabs
          v-model='tab'
          background-color='transparent'
        >
          <v-tabs-slider color='white' />
          <v-tab
            v-for='tab in tabs'
            :key='tab.id'
            @click='changeFilterStatus(tab.id)'
          >
            {{ tab.title }}
          </v-tab>
        </v-tabs>
      </template>
    </v-app-bar>

    <v-content>
      <v-container fluid v-bind:class="[isLoginPage ? 'fill-height' : '' ]">
        <router-view />
        <filter-menu
          :dialog="getShowFilterPage"
          v-on:closeMenu="closeModal($event)"
          v-on:saveValues="applyFilters($event)"
        />
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import filterMenu from './components/FilterMenu';

export default {
  components: {
    filterMenu,
  },

  name: 'App',

  watch: {
    inputSearch(newValue) {
      this.$store.commit('SET_SEARCH_FILTER', newValue);
    }
  },

  computed: {
    getShowFilterPage() {
      return this.showFilterPage;
    },

    isListPage() {
      return this.$route.name === 'Listagem';
    },

    isLoginPage() {
      return this.$route.name === 'Login';
    },
  },

  methods: {
    closeModal($event) {
      this.showFilterPage = false;
    },

    applyFilters($event) {
      this.showFilterPage = false;
    },

    changeFilterStatus(status) {
      this.$store.commit('SET_FILTER_STATUS', status);
    },

    findvalue() {
      if (this.inputSearch === '') {
        this.showSearchInput = !this.showSearchInput;
        setTimeout(() => {
          this.$refs.inputSearch.focus();
        }, 100);
      }
    },

    logout() {
      localStorage.removeItem('token');
      this.$router.push('/login');
    }
  },

  data() {
    return {
      showSearchInput: true,
      tab: null,
      inputSearch: '',
      showFilterPage: false,
      tabs: [
        {
          id: 0,
          title: 'Todos',
        },
        {
          id: 2,
          title: 'Em Andamento',
        },
        {
          id: 3,
          title: 'Em Análise',
        },
      ],
      drawer: null,
      menu: [
        { title: 'Dashboard', icon: 'mdi-view-dashboard' },
        { title: 'Ordens de Manutenção', icon: 'mdi-clipboard-text-multiple', to: '/order' },
        { title: 'Cadastros', icon: 'mdi-message-draw', class:'sm-hide md-block', to: '/register'},      
        { title: 'Usuários', icon: 'mdi-account-multiple-plus', to: '/user' },
        { title: 'Defeitos', icon: 'mdi-bandage', class: 'sm-hide md-block', to: '/defect' },
        { title: 'Local de instalação', icon: 'mdi-home-city', class: 'sm-hide md-block', to: '/local-install' },
        { title: 'Tipo de Manutenção', icon: 'mdi-file-cog', class: 'sm-hide md-block', to: '/maintenance' },
        { title: 'Equipamentos', icon: 'mdi-tools', class: 'sm-hide md-block', to: '/equipments' },
        { title: 'Ordem de Manutenção', icon: 'mdi-clipboard-text', class: 'Testando', to: '/new_order' },
        { title: 'Epis', icon: 'mdi-sunglasses', class: 'sm-hide md-block', to: '/epis' },
        { title: 'Layouts', icon: 'mdi-collage', class: 'Testando', to: '/layout' },
        { title: 'Operações', icon: 'mdi-collage', class: 'Testando', to: '/operation' },
        { title: 'Materiais', icon: 'mdi-collage', class: 'Testando', to: '/material' }
      ],
      
    }
  },
};
</script>
